@import url(https://fonts.googleapis.com/css2?family=Grandstander:ital,wght@0,500;0,600;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: "Grandstander:ital", cursive;
  margin: 0;
  padding: 0;
  background-color: black;
  min-height: 100vh;
  text-transform: uppercase;
}
.container {
  padding: 1rem 2rem;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
}
textarea {
  outline: none;
  border: none;
  border-radius: 1rem;
  font-size: 2.3rem;
  min-width: 40vw;
  min-height: 40vh;
  background-color: #91e019;
}
button {
  border: none;
  background: #91e019;
  font-size: 1.3rem;
  font-weight: 600;
  -webkit-text-emphasis-style: italic;
          text-emphasis-style: italic;
  border-radius: 0.2rem;
  padding: 0.5rem 0.8rem;
  transition: transform 0.5s;
}
button:hover,
button:focus {
  cursor: pointer;
  transform: scaleX(1.1);
  outline: none;
}
span {
  text-transform: lowercase;
}
button:disabled,
textarea:disabled {
  cursor: not-allowed;
  background-color: #223604;
}

